* {
  box-sizing: border-box;
  margin: 0;
}
.landing {
  text-align: center;
  width: 100%;
  height: 800px;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,0.7)), to(rgba(255,255,255,0.7))),
    url(/static/media/catdog.c7b811e4.jpg);
  background-image: linear-gradient(rgba(255,255,255,0.7), rgba(255,255,255,0.7)),
    url(/static/media/catdog.c7b811e4.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  justify-content: center;
  align-items: center;
  color: #010203;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  text-shadow: -1.2px 1.2px 0 #E2E5E8, 1.2px 1.2px 0 #E2E5E8, 1.2px -1.2px 0 #E2E5E8, -1.2px -1.2px 0 #E2E5E8;
}

h1 {
  font-family: 'Londrina Solid', cursive;
  font-size: 2.5em;
  padding: 5px;
}

h2 {
  font-size: 1.5em;
  padding: 5px;
}

h3 {
  font-weight: 600;
  font-size: 1.3em;
  padding: 5px;
}

p {
  font-size: 1em;
  line-height: 1.3em;
  padding: 5px;
}

button {
  border: 1px solid black;
  border-radius: 5px;
  padding: 5px;
  background-color: #E2E5E8;
}

button:hover {
  border: 2px solid black;
  font-weight: 900;
}


@media screen and (min-height: 800px) {
  .landing {
    height: 600px;
    margin-top: 40px;
  }
  h1 {
    font-size: 2.5em;
    padding: 5px;
    padding-top: 40px;
  }
}

@media screen and (min-height: 1000px) {
  h1 {
    padding-top: 100px;
  }
}
.adoption {
  display: block;
  justify-content: center;
  align-items: center;
  margin-right: auto;
  margin-left: auto;
  color: #010203;
  font-family: 'Montserrat', sans-serif;
}

.add-name-form {
  display: block;
  margin-right: auto;
  margin-left: auto;
  /* justify-content: center;
  align-items: center; */
  padding: 15px;
  margin: 10px;
}

.add-name-form > label {
  margin-right: 10px;
}

.add-name-form > input {
  width: 70%;
}

.add-name-button {
  display: block;
  margin-right: auto;
  margin-left: auto;
  margin-top: 5px;
}

h1 {
  text-align: center;
}

.pet-selection {
  margin: 10px;
}

.animal-image {
  max-width: 90%;
  display: block;
  margin-right: auto;
  margin-left: auto;
}

.adopt-button {
  font-size: 1.3em;
  display: block;
  margin-right: auto;
  margin-left: auto;
  margin-top: 5px;
  margin-bottom: 5px;
}

.adopted {
  text-align: center;
}

@media screen and (min-width: 1000px) {
  /* img to the left, info to the right */

  /* .pet-selection {
    display: flexbox;
    flex-direction: row;
    justify-content: space-between;
  } */

  .container {
    display: table;
    width: 100%;
    height: 500px;
  }

  .pet-info {
    position: relative;
    left: 50%;
    width: 50%;
    padding: 5px;
  }

  .animal-image {
    position: absolute;
    max-width: 50%;
    right: 50%;
  }

  .add-name-button {
    display: inline-block;
    margin-left: 10px;
  }

}

